.sendForm {
  padding-top: 56px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 44px;
}

.textarea {
  padding: 12px 24px;
  max-width: 100%;
  width: 100%;
  height: 40px;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 100%;
  color: rgba(0, 0, 0, 0.6);
  border-radius: 20px;
  box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.1), 0 5px 20px 0 rgba(29, 33, 38, 0.03);
  background: #f5f7fb;
  border: none;
  resize: none;

  &::placeholder {
      font-family: "Montserrat", sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 100%;
      color: rgba(0, 0, 0, 0.6);
  }

  &:focus {
      outline: 1px solid #d2d2d2;
  }
}

.button {
  width: 120px;
  height: 40px;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 16px;
  color: #fff;
  background-color: #06c;
  border-radius: 20px;
  border: none;
  cursor: pointer;
  transition: .3s ease-in-out;
}

.button:hover {
  background-color: #0055a9;
  /* transition: .3s ease-in-out; */
}
